.App {
  text-align: center;
  padding: 20px;
}

h1 {
  color: #333;
}

form {
  margin-bottom: 20px;
}

label {
  display: block;
  margin-bottom: 5px;
  color: #333;
}
.invoice-template {
  border: 1px solid #ccc;
  padding: 20px;
  margin-bottom: 20px;
}

.invoice-template h2 {
  color: #333;
  margin-bottom: 10px;
}

.invoice-template p {
  color: #666;
  margin-bottom: 5px;
}
input[type="text"],
input[type="email"] {
  width: 30%;
  padding: 8px;
  border: 1px solid lightgray;
  border-radius: 4px;
  margin-bottom: 10px;
}

button {
  background-color: lightblue;
  color: black;
  border: none;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 4px;
  cursor: pointer;
}

.invoice {
  background-color: #f2f2f2;
  padding: 20px;
  border-radius: 4px;
  margin-bottom: 20px;
  text-align: center;
}

.invoice h2 {
  color: #333;
  margin-bottom: 10px;
}

.invoice p {
  color: #666;
  margin-bottom: 5px;
}
.Invoice-details{
  display: inline-block;
  text-align: center;
}